    }
    .outer {
      // list-style-type: decimal;
      font-size: 16px;
      color: #696969;
      line-height: 48px;
      // li {
      //   list-style-type: decimal;
      // }
      > li > p {
        font-weight: 700;
        height: 48px;
        border-top: 1px solid #ccc;
      }
      .item {
        width: 23%;
        text-align: center;
      }
      .item:first-child {
        width: 30%;
        text-align: left;
        // padding-left: 13%;
        box-sizing: border-box;
        .ellipsis {
          text-align: left;
        }
      }
      // 大屏
      @media screen and(min-width: 1601px) {
        .item:first-child {
          padding-left: 13%;
        }
      }
      // 小屏
      @media screen and(max-width: 1600px) {
        .item:first-child {
          padding-left: 12%;
        }
      }
    }
    .inner {
      font-size: 16px;
      color: #696969;
      line-height: 48px;
      // letter-spacing: -0.5px;
      // max-width: 427px;
      span {
        box-sizing: border-box;
      }
      li {
        box-sizing: border-box;

        height: 48px;
        border-top: 1px solid #ccc;
        .item1 {
          width: 23%;
          text-align: center;
        }
        .item1:first-child {
          width: 30%;
          text-align: left;
          // padding-left: 13%;
          box-sizing: border-box;
          .ellipsis {
            text-align: left;
          }
        }
        // 大屏
        @media screen and(min-width: 1601px) {
          .item1:first-child {
            padding-left: 14%;
          }
        }
        // 小屏
        @media screen and(max-width: 1600px) {
          .item1:first-child {
            padding-left: 13%;
          }
        }
      }
    }
    // 表格头部
    .tableHead {
      height: 48px;
      line-height: 48px;
      text-align: center;
      background-color: #ebebeb;
      font-weight: 700;
      span.fl {
        width: 23%;
      }
      span.fl:first-child {
        width: 30%;
      }
    }
    .others {
      box-sizing: border-box;
      font-weight: 700;
      height: 48px;
      line-height: 48px;
      border-top: 1px solid #ccc;
      .plus,
      .minus {
        position: absolute;
        font-size: 24px;
        color: #63b4ff;
        cursor: pointer;
        height: 48px;
        line-height: 48px;
      }
      .plus {
        right: 10px;
      }
      .minus {
        right: -18px;
      }
      span.fl {
        width: 23%;
        text-align: center;
      }
      span.fl:first-child {
        position: relative;
        width: 30%;
        text-align: left;
        // padding-left: 13%;
        box-sizing: border-box;
        .ellipsis {
          text-align: left;
        }
      }
      // 大屏
      @media screen and(min-width: 1601px) {
        span.fl:first-child {
          padding-left: 13%;
        }
      }
      // 小屏
      @media screen and(max-width: 1600px) {
        span.fl:first-child {
          padding-left: 12%;
        }
      }
    }
    // 表格底部
    .tableFoot {
      height: 48px;
      line-height: 48px;
      text-align: center;
      background-color: #fff1f0;
      font-weight: 700;
      span.fl {
        width: 23%;
      }
      span.fl:first-child {
        width: 30%;
      }
    }

    .el-table .cell,
    .el-table th div,
    .el-table--border td:first-child .cell,
    .el-table--border th:first-child .cell {
      padding-left: 60px;
      font-size: 16px;
    }
    .el-table thead {
      color: #646464;
    }
    .el-table__row td:first-child {
      font-weight: 700;
    }
    .el-table__body-wrapper tbody tr:first-child {
      background: #fff1f0;
      border: 1px solid #ee6723;
    }
    .dt-square-btn {
      width: 34px;
      height: 34px;
      line-height: 30px;
    }
  }
  // 第七个tab
  .sixth {
    .el-table .cell {
      text-align: center;
      font-size: 16px;
    }
    .dt-txt-btn {
      margin-right: 16px;
    }
  }
}
</style>





